import React, { useState } from "react";


export const FAQ = () => {

    let Qs = [
        'Kedy a kde bude festival?',
        'Ak je vstupn?',
        'D sa na festival prs autom?',
        'Je festival vhodn aj pre rodiny s demi?',
        'Bude na festivale oberstvenie?',
        'Mem prs na festival so psom?',
        'Ako mem festival podpori?'
    ]
    let As = [
        'Festival bude 26.8. od 14:00 v amfitetri v Dudinciach.',
        'Chceme, aby bol festival dostupn pre kadho bez ohadu na jeho rozpoet. Na festivale mme preto dobrovon vstupn.',
        'no. Parkova sa d pri futbalovom ihrisku. Dobrovonci zodpovedn za parkovanie vs usmernia.',
        'no! Pre deti mme nachystan velijak program, atrakcie a cukrov vatu zadarmo.',
        'Samozrejme. Na festivale bude viacero stnkov s jedlom a npojmi.',
        'Kee je na festivale hluk a vea det, asi nebude najlepm miestom pre vaeho pska. Prinies ho napriek tomu mete, ale povaujeme za samozrejmos, e budete udriava arel festivalu v istote.',
        'V prvom rade modlitbou! Ale poteme sa aj akejkovek inej forme pomoci - dobrovonctvo, zdieanie na socilnych sieach, pozvanie znmych i finann podpora. Finanne ns mete podpori prspevkom na n transparentn et: SK9183300000002402570697 akujeme!'
    ]

    const [toggle, setToggle] = useState([false, false, false, false, false, false, false])

    const toggleState = (index) => {
        console.log('hi')
        let array = [...toggle]
        array[index] = !toggle[index]
        setToggle(array)
    }

    return (
        <div className="content_container">
            <div className="home">
                
                <div className='intro'>
                    <h1 className="sectionName">ast otzky</h1>
                </div>
            
                <section id="faq">
                    <div className="faq">
                        {
                            [...Qs].map((val,i) => {
                                return (
                                    <div>
                                        <div className="Q" onClick={() => toggleState(i)} >{val} </div>
                                        {toggle[i] && <div className="A">{As[i]}</div>}
                                    
                                    </div>

                                )
                            })
                        }
                    </div>
                </section>
            </div>
        </div>       
    )
}